body {
  font-family: $fontFamily !important;
  font-size: $baseFontSize !important;
  line-height: 160% !important;
  color: $baseFontColor !important;
  background: $bodyBGColor !important;
}

// Page title
.region-page-title{
  h1{
    margin: 30px 0;
  }
}

// Footer Widget
.footerwidget{
  background: #108441;
  color: #86b59a;
  padding: 20px 0;
  border-top: 1px solid #f7f7f7;

  h2.title{
    color: #fff;
  }

  .left{
    float: left;
    margin: 10px 10px 0 0;
  }
}

.copyright{
  background: #000;
  text-align: center;
}

// Google Map
.map-and-address{
  h2.title{
    display: none;
  }
}

.google_map{

  p, .block{ margin: 0; }

  iframe{ display: block;	}
}


// Social media
.footer-menu{
  margin: 0;
  border: 0;
  background: image-url("social-media-bg.png");
  color: #fff;
  background-size: cover;
  padding: 80px 0;
}

.social-media{
  padding: 10px 0;
  text-align: center;

  a {
    line-height: 100%;
    font-size: 1em;
    display: inline-block;
    padding: 4px 8px;
    width: 26px;

    &:hover{
      color: $linkColorHover;
    }
  }
}


// Form
input, button, select, textarea{
  padding: 6px;
  border: 1px solid #e3e3e3;
}

.contact-message-form label{
  display: block;
}

body:not([class="header"]) {
  .btn,
  .node-readmore a,
  .flexslider .more-link,
  input[type="submit"] {
    @include globalButton();
  }

  .btn:hover,
  .node-readmore a:hover,
  .flexslider .more-link:hover,
  input[type="submit"]:hover{
    @include globalButtonHover();
  }
}

// Comment
.comment-wrap{
  overflow: hidden;
  margin-bottom: 20px;
  padding-bottom: 20px;
  border-bottom: 1px solid #F5F5F5;

  .author-details{
    float: left;
    width: 20%;
  }
  .author-comments{
    padding-left: 5px;
    float: left;
    width: 80%;

    h3{ margin-top: 10px; }

    p{ margin-bottom: 10px;	}
  }

}

.comment-submitted{
  padding: 5px 10px;
  border: 1px solid #eee;
  display: inline-block;
  line-height: 100%;
}


// links Inline
ul.links.inline li{
  display: inline-block;
  padding-right: 10px;
}

// Sidebar Block
.sidebar{
  background: #fff;
  padding: 15px;

  .region {
    h2.title{
      padding: 5px 0;
      margin: 0;
    }
    .content{ padding: 10px 0; }
  }
}

.region-content{
  min-height: 300px;
}

.path-frontpage{
  .region-content{
    min-height: auto;
  }
}

// Markup generated by Form API
.form-item,
.form-actions {
  margin-top: 1em;
  margin-bottom: 1em;
}

tr.odd .form-item,
tr.even .form-item {
  margin-top: 0;
  margin-bottom: 0;
}

.form-composite > .fieldset-wrapper > .description,
.form-item .description {
  font-size: 0.70em;
  line-height: 150%;
}

label.option {
  display: inline;
  font-weight: normal;
}

.form-composite > legend,
.label {
  display:inline;
  font-size: inherit;
  font-weight: bold;
  margin: 0;
  padding: 0;
}

.form-checkboxes .form-item,
.form-radios .form-item {
  margin-top: 0.4em;
  margin-bottom: 0.4em;
}

.marker {
  color: #e00;
}

abbr.tabledrag-changed,
abbr.ajax-changed {
  border-bottom: none;
}

.form-item input.error,
.form-item textarea.error,
.form-item select.error {
  border: 2px solid red;
}

.owl-theme .owl-dots .owl-dot span {
  background: #b5b5b5;
  height: 7px;
  width: 7px;
}
