.path-frontpage {
  .main-content {
    background-image: url("images/symbole-agrisud-2.png"), linear-gradient(#eff3f0, white);;//linear-gradient(#eff3f0, #fff);;
    background-repeat: no-repeat;
    background-position: unset;
    background-size: inherit;
    #block-messageaccueil {
      width: 70% !important;
      margin: 0 15% 0 !important;
      padding: 2rem !important;
      margin-bottom: 6rem !important;
      text-align: center;
    }
  }
  #midwidget {
    background-image: url("images/symbole-agrisud-2.png"), url("images/fermier-rescaled.png");
    background-repeat: no-repeat;
    background-position: right, top;
    background-size: inherit;
    margin-top: -8rem;
    padding: 5rem;

    .elpacho {
      position: relative;
      background: #fff;
      padding: 20px 20px;
      margin-bottom: 30px;
      border: 1px solid #eee;
      border-bottom: 4px solid $red-agrisud;
      text-align: center !important;
    }
  }
  //****Start block chiffres
  .region.region-updates-first {
    padding: 0 !important;
  }
  //***** End block chiffres
}

.path-nos-actualites {
  .region-content {
    background-image: url("images/actualite-background-90.png");
    background-repeat: no-repeat;
    background-position: top;
    //background-size: cover;
    margin-bottom: 2*$grid-gutter !important;
  }
  //.node--type-actualite.node--view-mode-teaser.ds-2col
  @include viewsListds2();
  @include designActualitesFields();
}

.path-liste-actions {
  #block-carte {
    margin-top: 2*$grid-gutter !important;
  }
  #page-title {
    background: none !important;
    height: 0 !important;
  }
  @include viewsListds2();
  @include designActionsFields;
}

#bottom-widget {
  width: 50% !important;
  margin-left: 25%;
  background: none !important;
  //  margin-top: 3rem;
  h2.title, .content * {
    color: $green-agrisud !important;
  }
