  //block-content.block-content--type-block-bouton.block-content--view-mode-full.ds-1col
  .block-content.block-content--type-block-links.block-content--view-mode-full.ds-1col,
  .block-content.block-content--type-block-bouton.block-content--view-mode-full.ds-1col {
    text-align: center;
    .field--name-body.field a {
      text-align: center;
      color: white !important;
      padding: 2rem 3rem;
      border: 1px solid $light-green !important;
      background-color: $light-green !important;
      font-weight: 600;
      display: block;
      width: 20rem;
      margin-top: 0 !important;
    }
  }
  .region-bottom-third {
    .block {
      margin: 0 !important;
    }
    .block-content.block-content--type-block-links.block-content--view-mode-full.ds-1col,
    .block-content.block-content--type-block-bouton.block-content--view-mode-full.ds-1col {
      float: left;
    }
  }
  .region-bottom-second {
    .block {
      margin: 0 !important;
    }
    .block-content.block-content--type-block-links.block-content--view-mode-full.ds-1col,
    .block-content.block-content--type-block-bouton.block-content--view-mode-full.ds-1col {
      float: right;
    }
  }
