.page-node-type-nous-connaitre {
  .region.region-updates-first {
    padding: 0 !important;
    @include blocksChiffres();
    @include blocksChiffresPageNousConnaitre();
  }
  #block-phrasecharteagrisud {
    margin-bottom: 3rem !important;
    .block-content.block-content--type-block-message {
      width: 70%;
      margin: 0 15% 0;
      text-align: center;
      font-family: "Roboto", Arial, sans-serif !important;
      font-weight: 100 !important;
      font-size: 2rem !important;
      line-height: 1.5 !important;
      margin-top: $grid-gutter !important;
      padding: 0 !important;
      margin-bottom: 0 !important;
    }
  }
}

