.updates,
.topwidget,
.top-message,
.footer,
.midwidget,
.bottom-widget,
.main-content,
.services,
.products,
.features,
.copyright,
.clients{
  *:not(i) {
    font-family: $fontFamily !important;
  }
  .block {
    margin-bottom: 2*$grid-gutter !important;
    header {
      margin-bottom: $grid-gutter/3 !important;
    }
  }

}
.header {
  .region-primary-menu ul, ul.links {
    li * {
      font-family: $fontFamily !important;
    }
  }
}

// Bottom Widgets
.bottom-widget{
  background: $green-agrisud !important;
  text-align: center;

  h2.title, *{
    color: $white;
  }

}


.main-content{
  padding: 0 0 50px 0;
}

.top-message,
.services{
  text-align: center;
}

// Front Page
.path-frontpage .feed-icon{
  display: none;
}

.updates{
  .region{
    background: none !important;
  }
}

.features{
  background-color: #fff2e0;
  background: url('../images/bg-features.jpg');
  background-size: cover;
}

.footer{
  tbody tr:hover, tbody tr:focus {
    background: none;
  }
}

.clients{
  background: #eee;

  .region{
    ul {
      li{
        display: inline-block;
        width: 100%;
        text-align: center;

        img{
          max-width: 200px;
        }
      }
    }
  }
}

// Testimonials
.testimonials-wrap{
  background: #fafafa;
  border-top: solid 1px #e3e3e3;
  border-bottom: solid 1px #e3e3e3;
  padding: 20px 0;
  text-align: center;
  font-size: 1em;
  line-height: 160%;
}

.region-testimonials{
  max-width: 600px;
  margin: 0 auto;
}

.testimonials-pic{
  width: 40px;
  height: 40px;
  display: inline-block;
  vertical-align: bottom;
  margin-right: 10px;
}

.testimonials-author{
  padding: 0 5px;
  font-size: .75em;
  color: #555;
}

// Price
.price-table{
  .card-header{
    border: 0;
    padding: 20px 0;
  }
  ul li {
    list-style: none;
    border-bottom: 1px solid #eee;
    padding-bottom: 10px;
    margin-bottom: 10px;
  }
}

// Team
.team{
  .team-list{
    .region{
      background: #efefef;
      overflow: hidden;
      padding: 10px;
      border: 1px solid #eaeaea;
    }
  }
}
